import React from "react";
import PropTypes from "prop-types";
import { Link as GatsbyLink } from "gatsby";
import styled from "styled-components";
import { device } from "../styles/variables";
import githubIcon from "../images/icon_github.svg";
import linkedInIcon from "../images/icon_linkedin.svg";

const Container = styled.div`
  margin: 20px 0 40px;
  padding: 0;
  width: 100%;
  text-align: center;
  a {
    color: inherit;
    text-decoration: none;
  }
`;
const Title = styled.h1`
color: #444
  display: block;
  font-size: 45px;
  font-weight: 600;
  letter-spacing: 2px;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
  @media ${device.tablet} {
    font-size: 50px;
  }
`;
// const isSelected = name =>
//   window.location.pathname && window.location.pathname.indexOf(`/${name}/`) > -1
const LinkName = styled.p`
  color: ${(props) => (props.isSelected ? props.fontColor : "#222")};
  display: inline-block;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 1.4;
  margin: 0;
  margin-right: 10px;
  margin-top: 10px;
  padding: 0;
  text-align: center;
  text-decoration: none;
  text-transform: capitalize;

  @media ${device.tablet} {
    font-size: 18px;
  }
`;
const LinkIcon = styled.img`
  display: inline-block;
  margin: 0;
  margin-right: 10px;
  padding: 0;
  text-decoration: none;
  width: 20px;
`;

const menuList = [
  {
    name: "about",
    fontColor: "#3DA2FF",
  },
  {
    name: "blog",
    fontColor: "#eb5c58",
  },
  // {
  //   name: "podcast",
  //   fontColor: "#83B00A",
  // },
];

const Link = ({ details }) => {
  const { name, fontColor } = details;
  const path = typeof window !== "undefined" ? window.location.pathname : "";
  const isSelected = `/${name}/` === path;
  // .indexOf(`/${name}/`) > -1
  return (
    <GatsbyLink to={`/${name}/`}>
      <LinkName isSelected={isSelected} fontColor={fontColor}>
        {name}
      </LinkName>
    </GatsbyLink>
  );
};

Link.propTypes = {
  details: PropTypes.object.isRequired,
};

export const Header = () => {
  return (
    <Container>
      <GatsbyLink to="/">
        <Title>Jack Murphy</Title>
      </GatsbyLink>
      <div>
        {menuList &&
          typeof menuList !== "undefined" &&
          menuList.map((link, idx) => <Link key={idx} details={link} />)}

        <a href="https://github.com/murpjack" rel="noreferrer" target="_blank">
          <LinkIcon src={githubIcon} alt="Github" />
        </a>
        <a
          href="https://www.linkedin.com/in/jack-murphy-bb0426155/"
          rel="noreferrer"
          target="_blank"
        >
          <LinkIcon src={linkedInIcon} alt="LinkedIn" />
        </a>
      </div>
    </Container>
  );
};
export default Header;
